*,
*:before,
*:after{
    box-sizing: border-box;
}

body {
    display: flex;
    flex-direction: column;
    height: 100%;
    min-width: 300px;
    overflow-x: hidden;
    margin: 0;
}

main {
    flex: 1 0 auto;
    min-height: 400px;
}

a {
    color: #fff;
    text-decoration: none;
    position: relative;

    &::before {
        position: absolute;
        content: '';
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        opacity: 0;
        -webkit-transition: all .5s ease;
        -moz-transition: all .5s ease;
        -ms-transition: all .5s ease;
        -o-transition: all .5s ease;
        transition: all .5s ease;
    }
}

img {
    object-fit: cover;
}

li {
    list-style: none;
}

g {
    cursor: pointer;
}

.d_none {
    display: none !important;
}

.hidden {
    opacity: 0;
}

.visible {
    opacity: 1;
}

.scroll-wrapper {
    @include max-screen(1024px) {
        overflow-x: scroll;
    }
}

.address {
    font-size: 14px;
}

h1 {
    @include oswald_700;
}

h2 {
    @include oswald_700;
}

h3 {
    @include monts_700;
    text-transform: uppercase;
}

p {
    @include monts_400;
}

// Прелоадер
#preloader {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 99999;
    background: $light-blue; /* Old browsers */
    background: -moz-linear-gradient(35deg, $medium-blue 20%, $dark_blue 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(35deg, $medium-blue 20%, $dark_blue 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(35deg, $medium-blue 20%, $dark_blue 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='00A3E4', endColorstr='#006ABB',GradientType=0 ); /* IE6-9 */

    overflow: visible;
    img {
        animation: flipInY 2s ease-in;
    }
}

.flat-arrow {
    position: relative;
    z-index: 10;

    &_middle {
        margin: 0 -14px;
        -webkit-transition: margin .5s ease;
        -moz-transition: margin .5s ease;
        -ms-transition: margin .5s ease;
        -o-transition: margin .5s ease;
        transition: margin .5s ease;
    }
}

// Стиль для общего Шаблона страниц
.section_capt {
    max-width: 1200px;
    margin: 0 auto 26px;

    @include max-screen(768px) {
        margin: 0 auto;
        padding: 35px;
        position: relative;

        &::before {
            position: absolute;
            content: '';
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background: url(/images/images-kamchatka/promo/promo1.png) 50% 50% no-repeat;
            background-size: cover;
            z-index: -1;
        }
    }

    h1 {
        position: relative;
        top: -5px;
        @include oswald_700;
        color: #fff;
        font-size: 40px;
        line-height: 35px;
        margin: 0;

        @include max-screen(576px) {
            margin-top: 10px;
            text-align: center;
        }
    }

    .row {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding-left: 65px;

        @include max-screen(576px) {
            flex-direction: column;
            padding-left: 0;
        }
    }

    .logo {
        padding-right: 25px;

        @include max-screen(576px) {
            padding-right: 0;
        }
    }

    .title {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 300px;
        padding-bottom: 30px;

        & > h2 {
            font-size: 45px;
            color: $dark-blue;
            text-shadow: 2px 2px 4px #000;
        }
    }
}

// Общие настройки внутренних страниц
.secondary-page {
    max-width: 1200px;
    padding: 0;
    margin: 0 auto;
}